const newArray = require("new-array");
const randomFloat = require("random-float");
const randomInt = require("random-int");
const randomVector = require("gl-vec3/random");
const randomRadian = () => randomFloat(-Math.PI, Math.PI);
const randomRotation = () => newArray(3).map(randomRadian);
const simplex = new(require("simplex-noise"))();

var CreatorService = {
  data() {
    return {
      geometryMAP: {
        1: "IcosahedronGeometry",
        2: "OctahedronGeometry",
        3: "TorusKnotGeometry"
      }
    };
  },
  methods: {
    addGrid(reverse) {
      // create grid object
      this.view.components.grid = new THREE.Object3D();

      // create single grid
      var grid1 = new THREE.GridHelper(10, 100, this.view.colors.directional, this.view.colors.directional);
      grid1.position.y = reverse ? 15 : -15
      grid1.position.z = -1000
      grid1.scale.multiplyScalar(100)

      var grid2 = new THREE.GridHelper(10, 100, this.view.colors.directional, this.view.colors.directional);
      grid2.position.y = reverse ? 15 : -15
      grid2.position.z = 0
      grid2.scale.multiplyScalar(100)

      var grid3 = new THREE.GridHelper(10, 100, this.view.colors.directional, this.view.colors.directional);
      grid3.position.y = reverse ? 15 : -15
      grid3.position.z = 1000
      grid3.scale.multiplyScalar(100)

      // add grids to main grid
      this.view.components.grid.add(grid1)
      this.view.components.grid.add(grid2)
      this.view.components.grid.add(grid3)

      // add main grid to scene
      this.view.scene.add(this.view.components.grid);
    },
    addLights(ambientColor, directionalColor) {
      // add ambient light
      this.view.components.ambientLight = new THREE.AmbientLight(ambientColor);
      this.view.scene.add(this.view.components.ambientLight);

      // add first direction light
      this.view.components.lights[0] = new THREE.DirectionalLight(
        directionalColor,
        1
      );
      this.view.components.lights[0].position.set(0, 1, 1);
      this.view.scene.add(this.view.components.lights[0]);
    },
    addRocks(double, reverse, specular, number, color) {
      // create rocks object
      this.view.components.rocks = new THREE.Object3D();

      // iterate to number
      for (var i = 0; i < number; i++) {
        // create single rock geometry
        var width = randomInt(2, 6)
        var height = randomInt(5, 20)
        var geometry = new THREE.ConeGeometry(width, height, 6);

        // create single rock material
        var material = new THREE.MeshPhongMaterial({
          color: color,
          flatShading: true,
        });

        // create mesh
        var mesh = new THREE.Mesh(geometry, material.clone());
        var posY = reverse ? (15 - (height / 2)) : -(15 - (height / 2));

        mesh.position.fromArray([
          randomFloat(5, this.music.audio.fftSize / 4), posY,
          randomFloat(this.music.audio.fftSize / 2) - this.music.audio.fftSize / 4
        ]);
        mesh.rotation.z = reverse ? Math.PI : 0;

        // create specular mesh
        var meshClone = mesh.clone()
        meshClone.position.x *= -1

        // add particle to main mesh
        if (double) {
          var meshCl1 = mesh.clone()
          meshCl1.rotation.z = Math.PI
          meshCl1.position.y = 60 - (height / 2);
          mesh.position.y = -(60 - (height / 2));

          var meshCl2 = meshClone.clone()
          meshCl2.rotation.z = Math.PI
          meshCl2.position.y = 60 - (height / 2);
          meshClone.position.y = -(60 - (height / 2));

          if (specular) {
            var rand1 = randomFloat(5, this.music.audio.fftSize / 8)
            mesh.position.x = -60
            mesh.position.y = rand1
            mesh.rotation.z = -Math.PI / 2

            meshClone.position.x = 60
            meshClone.position.y = rand1
            meshClone.rotation.z = Math.PI / 2

            meshCl1.position.x = -60
            meshCl1.position.y = -rand1
            meshCl1.rotation.z = -Math.PI / 2

            meshCl2.position.x = 60
            meshCl2.position.y = -rand1
            meshCl2.rotation.z = Math.PI / 2
          }

          this.view.components.rocks.add(mesh);
          this.view.components.rocks.add(meshClone);
          this.view.components.rocks.add(meshCl1);
          this.view.components.rocks.add(meshCl2);
        } else {
          this.view.components.rocks.add(mesh);
          this.view.components.rocks.add(meshClone);
        }
      }

      // add rocks to scene
      this.view.scene.add(this.view.components.rocks)
    },
    addParticles(double, number, color, reverse) {
      // create particle object
      this.view.components.particles = new THREE.Object3D();
      var particlesUp = new THREE.Object3D()
      var particlesDown = new THREE.Object3D()

      // create single particle geometry
      function generateGeometry() {
        const geometry = new THREE.TetrahedronGeometry(1, randomInt(1, 2));
        geometry.vertices.forEach(v => {
          let steps = 3;
          let s = Math.pow(2, steps);
          let a = 0.75;
          for (let i = 0; i < steps; i++) {
            v.x += a * simplex.noise3D(v.x * s * 0, v.y * s, v.z * s);
            v.y += a * simplex.noise3D(v.x * s, v.y * s * 0, v.z * s);
            v.z += a * simplex.noise3D(v.x * s, v.y * s, v.z * s * 0);
            s *= 0.25;
            a *= 1 / steps * i;
          }
        });
        geometry.computeFaceNormals();
        geometry.verticesNeedsUpdate = true;
        return geometry;
      }

      // create geometries for particles
      var geometries = newArray(10).map(generateGeometry);

      // create single particle material
      var material = new THREE.MeshPhongMaterial({
        color: color,
        wireframe: true,
        side: THREE.DoubleSide
      });

      // create particles
      for (var i = 0; i < number; i++) {
        var geometry = geometries[randomInt(geometries.length - 1)];
        var mesh = new THREE.Mesh(geometry, material.clone());
        mesh.material.opacity = randomFloat(0.05, 0.1);
        mesh.scale.multiplyScalar(randomFloat(0.1, 1.5));
        mesh.rotation.fromArray(randomRotation());
        mesh.direction = new THREE.Vector3().fromArray(randomVector([]));
        var posY = reverse ? -randomFloat(this.music.audio.fftSize / 8) : randomFloat(this.music.audio.fftSize / 8)
        mesh.position.fromArray([
          randomFloat(this.music.audio.fftSize / 4) - this.music.audio.fftSize / 8,
          posY,
          randomFloat(this.music.audio.fftSize / 4) - this.music.audio.fftSize / 8
        ]);

        // double particles ?
        if (double) {
          var meshClone = mesh.clone()
          meshClone.position.y = reverse ? randomFloat(this.music.audio.fftSize / 8) : -randomFloat(this.music.audio.fftSize / 8)

          // add particle to parent object
          particlesUp.add(mesh)
          particlesDown.add(meshClone)
        } else {
          // add particle to main object
          this.view.components.particles.add(mesh);
        }
      }

      // add particles to main object
      if (double) {
        this.view.components.particles.add(particlesUp);
        this.view.components.particles.add(particlesDown);
      }

      // add particles to scene
      this.view.components.particles.visible = false
      this.view.scene.add(this.view.components.particles);
    },
    addBulb(double, reverse, position, internalColor, externalColor, type, faces, tube, segments, radials) {
      // create bulb object
      this.view.components.bulb = new THREE.Object3D();
      var bulbRight = new THREE.Object3D();
      var bulbLeft = new THREE.Object3D();

      // create bulb geometry
      var geometry = new THREE[this.geometryMAP[type]](15, faces, tube, segments, radials, 5, 5);

      // create bulb material
      var materialSkeleton = new THREE.MeshPhongMaterial({
        color: externalColor,
        wireframe: true
      });
      var materialFill = new THREE.MeshPhongMaterial({
        color: internalColor,
        flatShading: true
      });

      // create bulb mesh skeleton
      var meshSkeleton = new THREE.Mesh(geometry, materialSkeleton.clone());
      meshSkeleton.position.x = position.x;
      meshSkeleton.position.y = position.y;
      meshSkeleton.position.z = position.z;
      meshSkeleton.rotation.z = Math.PI * 1.49
      meshSkeleton.scale.x = meshSkeleton.scale.y = meshSkeleton.scale.z = 0.11;

      // create bulb mesh internal
      var meshFill = new THREE.Mesh(geometry, materialFill.clone());
      meshFill.position.x = position.x;
      meshFill.position.y = position.y;
      meshFill.position.z = position.z;
      meshFill.rotation.z = Math.PI * 1.49
      meshFill.scale.x = meshFill.scale.y = meshFill.scale.z = 0.1;

      // add meshes to parent
      if (double) {
        bulbRight.add(meshSkeleton)
        bulbRight.add(meshFill)
        bulbLeft.add(meshSkeleton.clone())
        bulbLeft.add(meshFill.clone())

        if (reverse) {
          bulbRight.position.y = 6
          bulbLeft.position.y = -6
        } else {
          bulbRight.position.x = 8
          bulbLeft.position.x = -8
        }


        this.view.components.bulb.add(bulbRight)
        this.view.components.bulb.add(bulbLeft)

      } else {
        this.view.components.bulb.add(meshSkeleton);
        this.view.components.bulb.add(meshFill);
      }

      // add bulb to scene
      this.view.scene.add(this.view.components.bulb);
    },
    addSongDetail(title, artist, color, position, sizes, trackNumber, transparent) {
      // create song detail object
      this.view.components.songDetail = new THREE.Object3D();

      // create text material
      var material = new THREE.MeshPhongMaterial({
        color: color
      });

      if (transparent) {
        material.transparent = true;
        material.opacity = 0;
      }

      // create geometry
      var geometryTitle = new THREE.TextGeometry(title, {
        font: this.view.font,
        size: sizes.title || 0.8,
        height: 0.1
      });
      geometryTitle.computeBoundingBox();

      var geometryArtist = new THREE.TextGeometry(artist, {
        font: this.view.font,
        size: sizes.artist || 0.5,
        height: 0.1
      });
      geometryArtist.computeBoundingBox();

      // create mesh title
      var meshTitle = new THREE.Mesh(geometryTitle, material);
      var centerOffsetTitle = -0.5 *
        (geometryTitle.boundingBox.max.x - geometryTitle.boundingBox.min.x);
      meshTitle.position.fromArray([centerOffsetTitle, -3, 0]);
      meshTitle.userData.trackNumber = trackNumber;

      // create mesh artist
      var meshArtist = new THREE.Mesh(geometryArtist, material);
      var centerOffsetArtist = -0.5 *
        (geometryArtist.boundingBox.max.x - geometryArtist.boundingBox.min.x);
      meshArtist.position.fromArray([centerOffsetArtist, -4, 0]);

      // add meshes to parent
      this.view.components.songDetail.add(meshTitle);
      this.view.components.songDetail.add(meshArtist);

      // add to scene
      this.view.components.songDetail.position.x = position.x || 0;
      this.view.components.songDetail.position.y = position.y;
      this.view.components.songDetail.position.z = position.z;
      this.view.scene.add(this.view.components.songDetail);

      // create bounding box
      var bbox = new THREE.BoxHelper(this.view.components.songDetail, 0xff0000);
      bbox.material.opacity = 0;
      bbox.material.alphaTest = 0.99

      // add song mesh
      bbox.add(this.view.components.songDetail)

      // add to array and scene
      this.view.scene.add(bbox);
      this.view.components.songs.push(bbox)
    },
    addAlbumCover(position) {
      var context = this;
      var geometry = new THREE.BoxGeometry(10, 10, 0.25);
      var material = new THREE.MeshPhongMaterial({
        color: 0xffffff,
      });

      var loader = new THREE.TextureLoader();
      loader.load('static/images/artwork.jpg',
        function (texture) {
          material.map = texture;
          material.transparent = true;
          context.view.components.cover = new THREE.Mesh(geometry, material);

          context.view.components.cover.position.x = position.x;
          context.view.components.cover.position.y = position.y;
          context.view.components.cover.position.z = position.z;

          context.view.scene.add(context.view.components.cover);
        }
      );
    }
  }
};
export default CreatorService;


