var EventService = {
  methods: {
    initEvents() {
      document.addEventListener("click", this.enableWakeLock, false);
      document.addEventListener('mousedown', this.onDocumentMouseDown, false);
      document.addEventListener('mousemove', this.onDocumentMouseMove, false);

      window.addEventListener("resize", this.onViewResize, false);
      window.addEventListener("orientationchange", function() {
        location.reload()
      }, false);
    },
    onViewResize() {
      this.view.camera.aspect = window.innerWidth / window.innerHeight;
      this.view.camera.updateProjectionMatrix();

      this.view.renderer.setSize(window.innerWidth, window.innerHeight);
    },
    onDocumentMouseDown(event) {
      event.preventDefault();

      if (this.view.mouse) {
        this.view.mouse.x = (event.clientX / this.view.renderer.domElement.clientWidth) * 2 - 1;
        this.view.mouse.y = -(event.clientY / this.view.renderer.domElement.clientHeight) * 2 + 1;

        this.view.raycaster.setFromCamera(this.view.mouse, this.view.camera);

        var intersects = this.view.raycaster.intersectObjects(this.view.components.songs, true);
        if (intersects.length > 0) {
          var song = intersects[0].object.children[0].children[0];
          if (song.userData.trackNumber) {
            var trackNumber = song.userData.trackNumber

            // navigate to track number view
            if (this.view.animations.cameraIsExecuted) {
              this.$router.push({
                path: 'tracks/' + trackNumber
              }, function () {
                location.reload()
              })
            }
          }
        }
      }
    },
    onDocumentMouseMove(event) {
      event.preventDefault();

      if (this.view.mouse) {
        this.view.mouse.x = (event.clientX / this.view.renderer.domElement.clientWidth) * 2 - 1;
        this.view.mouse.y = -(event.clientY / this.view.renderer.domElement.clientHeight) * 2 + 1;

        this.view.raycaster.linePrecision = 0.25
        this.view.raycaster.setFromCamera(this.view.mouse, this.view.camera);

        var intersects = this.view.raycaster.intersectObjects(this.view.components.songs, true);
        if (intersects.length > 0) {
          if (intersects[0].object.children.length > 0) {
            var song = intersects[0].object.children[0].children[0];

            // highlight only tracks
            if (song && song.userData.trackNumber && this.view.animations.cameraIsExecuted) {
              // change cursor icon
              document.body.style.cursor = 'pointer';

              // change title color
              song.material.color.setHex(0xbbbbbb);
            }
          }
        } else {
          for (var s in this.view.components.songs) {
            var song = this.view.components.songs[s].children[0].children[0]

            // reset cursor icon
            document.body.style.cursor = 'initial';

            // reset title color
            song.material.color.setHex(parseInt(this.view.colors.text));
          }
        }
      }

    },

  }
};
export default EventService;



