const audioFrequencyAverage = require("analyser-frequency-average");

var AnimatorService = {
  methods: {
    animateGrid() {
      this.view.components.grid.children[0].position.z += 1
      this.view.components.grid.children[1].position.z += 1
      this.view.components.grid.children[2].position.z += 1

      if (this.view.components.grid.children[0].position.z > 1000) {
        this.view.components.grid.children[0].position.z = -1000
      }
      if (this.view.components.grid.children[1].position.z > 1000) {
        this.view.components.grid.children[1].position.z = -1000
      }
      if (this.view.components.grid.children[2].position.z > 1000) {
        this.view.components.grid.children[2].position.z = -1000
      }
    },
    animateRocks() {
      // animate single rock
      for (var i = 0; i < this.view.components.rocks.children.length; i++) {
        // get rock
        var rock = this.view.components.rocks.children[i];

        // move rock
        rock.position.z += 1;

        if (rock.position.z > this.music.audio.fftSize / 8) {
          rock.position.z -= this.music.audio.fftSize / 2;
        }
      }
    },
    animateParticles(freqs, double) {
      // make particles visible
      this.view.components.particles.visible = true

      // animate single particle
      if (double) {
        // particles up
        for (var i = 0; i < this.view.components.particles.children[0].children.length; i++) {
          // get particle
          var particle = this.view.components.particles.children[0].children[i];

          // and move it forward dependent on the mouseY position.
          particle.position.z += 1 * 0.5;
          particle.rotation.z += 1 * 0.02;

          if (particle.position.z > this.music.audio.fftSize / 16) {
            particle.position.z -= this.music.audio.fftSize / 4;
          }

          // animate base on frequencies
          if (freqs[i] != 0) {
            particle.scale.setScalar(freqs[i] / 75)
          }
        }

        // particles down
        for (var i = 0; i < this.view.components.particles.children[1].children.length; i++) {
          // get particle
          var particle = this.view.components.particles.children[1].children[i];

          // and move it forward dependent on the mouseY position.
          particle.position.z += 1 * 0.5;
          particle.rotation.z += 1 * 0.02;

          if (particle.position.z > this.music.audio.fftSize / 16) {
            particle.position.z -= this.music.audio.fftSize / 4;
          }

          // animate base on frequencies
          if (freqs[i] != 0) {
            particle.scale.setScalar(freqs[i] / 75)
          }
        }

        // animate parent container
        this.view.components.particles.children[0].rotation.z += 0.0005
        this.view.components.particles.children[1].rotation.z += 0.0005
      } else {
        for (var i = 0; i < this.view.components.particles.children.length; i++) {
          // get particle
          var particle = this.view.components.particles.children[i];

          // and move it forward dependent on the mouseY position.
          particle.position.z += 1 * 0.5;
          particle.rotation.z += 1 * 0.02;

          if (particle.position.z > this.music.audio.fftSize / 16) {
            particle.position.z -= this.music.audio.fftSize / 4;
          }

          // animate base on frequencies
          if (freqs[i] != 0) {
            particle.scale.setScalar(freqs[i] / 75)
          }
        }
      }

    },
    animateBulb(double, type, freqs, max, increase) {
      switch (type) {
        case "forward":
          if (double) {
            this.view.components.bulb.children[0].children[0].rotation.x += 0.002;
            this.view.components.bulb.children[0].children[1].rotation.x += 0.002;

            this.view.components.bulb.children[1].children[0].rotation.x -= 0.002;
            this.view.components.bulb.children[1].children[1].rotation.x -= 0.002;
          } else {
            this.view.components.bulb.children[0].rotation.x += 0.002;
            this.view.components.bulb.children[1].rotation.x -= 0.002;
          }
          break;

        case "backward":
          if (double) {
            this.view.components.bulb.children[0].children[0].rotation.x -= 0.002;
            this.view.components.bulb.children[0].children[1].rotation.x -= 0.002;

            this.view.components.bulb.children[1].children[0].rotation.x += 0.002;
            this.view.components.bulb.children[1].children[1].rotation.x += 0.002;
          } else {
            this.view.components.bulb.children[0].rotation.x -= 0.002;
            this.view.components.bulb.children[1].rotation.x += 0.002;
          }

          break;

        case "pulse":
          // skeleton bulb
          if (double) {
            if (!freqs) {
              if (
                this.view.components.bulb.children[0].children[0] &&
                this.view.components.bulb.children[1].children[0].scale.x < max &&
                this.view.animations.pulseUp
              ) {
                this.view.components.bulb.children[0].children[0].scale.addScalar(increase);
                this.view.components.bulb.children[1].children[0].scale.addScalar(increase);
              } else if (this.view.components.bulb.children[0].children[0] && this.view.components.bulb.children[1].children[0].scale.x >= max) {
                this.view.animations.pulseUp = false;
                this.view.animations.pulseDown = true;
              }
              if (
                this.view.components.bulb.children[0].children[0] &&
                this.view.components.bulb.children[1].children[0].scale.x > 0.11 &&
                this.view.animations.pulseDown
              ) {
                this.view.components.bulb.children[0].children[0].scale.subScalar(increase);
                this.view.components.bulb.children[1].children[0].scale.subScalar(increase);
              } else if (this.view.components.bulb.children[0].children[0] && this.view.components.bulb.children[1].children[0].scale.x <= 0.11) {
                this.view.animations.pulseUp = true;
                this.view.animations.pulseDown = false;
              }
            } else {
              // internal bulb
              var avgSubBass = audioFrequencyAverage(
                this.music.audio.analyser.analyser,
                freqs,
                20,
                60
              );

              var avgBass = audioFrequencyAverage(
                this.music.audio.analyser.analyser,
                freqs,
                60,
                250
              );

              var avgLowMid = audioFrequencyAverage(
                this.music.audio.analyser.analyser,
                freqs,
                250,
                100
              );

              var avgHighMid = audioFrequencyAverage(
                this.music.audio.analyser.analyser,
                freqs,
                1000,
                2000
              );

              if (avgSubBass != 0) {
                this.view.components.bulb.children[0].children[0].scale.setScalar(avgSubBass / 10)
              } else {
                this.view.components.bulb.children[0].children[0].scale.setScalar(0.01)
              }

              if (avgBass != 0) {
                this.view.components.bulb.children[0].children[1].scale.setScalar(avgBass / 10)
              } else {
                this.view.components.bulb.children[0].children[1].scale.setScalar(0.01)
              }

              if (avgLowMid != 0) {
                this.view.components.bulb.children[1].children[0].scale.setScalar(avgLowMid / 10)
              } else {
                this.view.components.bulb.children[1].children[0].scale.setScalar(0.01)
              }

              if (avgHighMid != 0) {
                this.view.components.bulb.children[1].children[1].scale.setScalar(avgHighMid / 10)
              } else {
                this.view.components.bulb.children[1].children[1].scale.setScalar(0.01)
              }
            }
          } else {
            if (!freqs) {
              if (
                this.view.components.bulb.children[0].scale.x < max &&
                this.view.animations.pulseUp
              ) {
                this.view.components.bulb.children[0].scale.addScalar(increase);
              } else if (this.view.components.bulb.children[0].scale.x >= max) {
                this.view.animations.pulseUp = false;
                this.view.animations.pulseDown = true;
              }
              if (
                this.view.components.bulb.children[0].scale.x > 0.11 &&
                this.view.animations.pulseDown
              ) {
                this.view.components.bulb.children[0].scale.subScalar(increase);
              } else if (this.view.components.bulb.children[0].scale.x <= 0.11) {
                this.view.animations.pulseUp = true;
                this.view.animations.pulseDown = false;
              }
            } else {
              // internal bulb
              var avgBass = audioFrequencyAverage(
                this.music.audio.analyser.analyser,
                freqs,
                20,
                200
              );

              var avgMid = audioFrequencyAverage(
                this.music.audio.analyser.analyser,
                freqs,
                200,
                1000
              );

              if (avgBass != 0) {
                this.view.components.bulb.children[1].scale.setScalar(avgBass / 10)
              }
              if (avgMid != 0) {
                this.view.components.bulb.children[0].scale.setScalar(avgMid / 5)
              }
            }
          }


          break;
      }
    },
    animateCamera() {
      if (!this.view.animations.cameraIsExecuted &&
        this.view.camera.position.z >= 20
      ) {
        this.view.camera.position.z -= 0.25;
      } else {
        this.view.animations.cameraIsExecuted = true;
        this.view.controls.enableRotate = true;
      }
    },
    animateSongs() {
      for (var s in this.view.components.songs) {
        var song = this.view.components.songs[s].children[0].children[0];
        if (song.material.opacity < 1) {
          song.material.opacity += 0.0035;
        }
      }
    },
    animateAlbumCover() {
      if (this.view.components.cover.material && this.view.components.cover.material.opacity > 0) {
        this.view.components.cover.material.opacity -= 0.0035;
      } else {
        this.view.scene.remove(this.view.components.cover)
      }
    }
  }
};
export default AnimatorService;


