<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style>
html,
body {
  width: 100%;
  height: 100%;
  margin: 0px;
  overflow: hidden;
}

#app {
  font-family: "Dosis", "Helvetica Neue", "Helvetica", Helvetica, Arial,
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.loaders {
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0px;
  display: flex;
  flex-wrap: wrap;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.loader {
  min-width: 32px;
  color: #ddd;
}

.circle-loader {
  width: 32px;
  min-width: 32px;
  height: 32px;
  min-height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  border-radius: 50%;
  border-top: 5px solid rgba(255, 255, 255, 0);
  border-right: 5px solid #ddd;
  border-bottom: 5px solid #ddd;
  border-left: 5px solid #ddd;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: circle-loader-spin 1s infinite linear;
  animation: circle-loader-spin 1s infinite linear;
}

@keyframes circle-loader-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.white {
  color: #ddd;
}

.black {
  color: #4e4e4e;
}

.red {
  color: #cb3837;
}

div[data-popover="tooltip"] {
  background: #444;
  color: #f9f9f9;

  font-size: 14px;
}

#view {
  background-size: 100%;
  text-align: center;
  margin: 0px;
  overflow: hidden;
}

#fullscreen {
  position: absolute;
  top: 12px;
  right: 15px;
  cursor: pointer;
  z-index: 1;
}

#play {
  cursor: pointer;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 10px;
  z-index: 1;
}

#pause {
  cursor: pointer;
  position: absolute;
  top: 16px;
  left: 0;
  right: 0;
  z-index: 0;
}

#home {
  position: absolute;
  top: 12px;
  left: 15px;
  cursor: pointer;
  z-index: 1;
}

#headphones {
  cursor: pointer;
  position: absolute;
  top: 12px;
  left: 0;
  right: 0;
  z-index: 0;
}

#hint {
  color: #ddd;
  margin-right: 5px;
  vertical-align: super;
}
</style>


