var MusicService = {
  methods: {
    play() {
      if (this.music.audio.status === "playing") {
        this.music.audio.player.pause();
        this.music.audio.status = "paused";
      } else {
        this.music.audio.player.play();
        this.music.audio.status = "playing";
      }
    }
  }
};
export default MusicService;



