global.THREE = require("three");

require("./../lib/OrbitControls.js");

const audioPlayer = require("web-audio-player");
const audioAnalyser = require("web-audio-analyser");
const audioContext = require("ios-safe-audio-context");
const mobile = require("is-mobile");

var InitService = {
  methods: {
    initRenderer() {
      this.view.renderer = new THREE.WebGLRenderer({
        antialias: true,
        alpha: true
      });
      this.view.renderer.setPixelRatio(
        window.devicePixelRatio ? window.devicePixelRatio : 1
      );
      this.view.renderer.setSize(window.innerWidth, window.innerHeight);
      this.view.renderer.autoClear = false;
    },
    updateRenderer() {
      this.view.renderer.render(this.view.scene, this.view.camera);
    },
    initScene() {
      this.view.scene = new THREE.Scene();
    },
    initCamera(position) {
      this.view.camera = new THREE.PerspectiveCamera(
        50,
        window.innerWidth / window.innerHeight,
        1,
        500
      );
      this.view.camera.position.z = 100;
      this.view.camera.position.y = position.y;
      this.view.scene.add(this.view.camera);
    },
    initControls() {
      this.view.controls = new THREE.OrbitControls(
        this.view.camera,
        this.view.renderer.domElement,
        mobile()
      );
      this.view.controls.minDistance = 20;
      this.view.controls.maxDistance = 100;

      this.view.controls.enableRotate = false;
      this.view.controls.enableZoom = false;
      this.view.controls.enablePan = false;
    },
    initAmbient(context) {
      // add ambient light
      context.addLights(
        context.view.colors.ambient,
        context.view.colors.directional
      );

      // add song detail
      context.addSongDetail(
        context.music.title,
        context.music.artist,
        context.view.colors.text,
        context.music.position, {
          title: 0.8,
          artist: 0.5
        }
      );
    },
    initAudio(callback) {
      this.music.audio.context = audioContext();
      this.music.audio.player = audioPlayer(this.music.audio.source, {
        loop: true,
        context: this.music.audio.context
      });

      this.music.audio.analyser = audioAnalyser(
        this.music.audio.player.node,
        this.music.audio.context, {
          stereo: false,
          audible: false
        }
      );
      this.music.audio.analyser.analyser.fftSize = this.music.audio.fftSize;

      this.music.audio.player.on("load", () => {
        this.music.audio.ready = true;
        this.music.audio.player.node.connect(
          this.music.audio.player.context.destination
        );
        callback(this.music.audio.ready);
      });
    }
  }
};
export default InitService;



