import Vue from 'vue'
import Router from 'vue-router'

import Vitae from '@/components/Vitae'

import InAltoLoco from '@/components/InAltoLoco'
import Zahira from '@/components/Zahira'
import AlterEgo from '@/components/AlterEgo'
import Libra from '@/components/Libra'
import MementoMori from '@/components/MementoMori'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Vitae',
      component: Vitae
    },
    {
      path: '/tracks/1',
      name: 'InAltoLoco',
      component: InAltoLoco
    },
    {
      path: '/tracks/2',
      name: 'Zahira',
      component: Zahira
    },
    {
      path: '/tracks/3',
      name: 'AlterEgo',
      component: AlterEgo
    },
    {
      path: '/tracks/4',
      name: 'Libra',
      component: Libra
    },
    {
      path: '/tracks/5',
      name: 'MementoMori',
      component: MementoMori
    }
  ]
})



